body {
  text-align: center;
  text-transform: uppercase;
}

.assetTable {
  width: 50%;
  margin-left: auto;
  margin-right: auto;
}

.assetImg {
  height: 2rem;
  width: 2rem;
}

.navContainer {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.settingsContainer {
  position: fixed;
  top: 0;
  right: 0;
  /*background-color: lightblue;*/
  width: 6rem;
}

.avatar {
  /*background-color: indianred;*/
  /*border-radius: 3rem;*/
  margin: 0.2rem;
  padding: 0.2rem;
  height: 3rem;
  width: 3rem;
}

.settingsContainer > button {
  margin-top: 4rem;
  width: 100%;
  margin: 0.25rem;
  padding: 0.25rem;
}

.correct {
  background-color: lightgreen;
  /*filter: drop-shadow(0 0 0.75rem lightgreen)*/
}

.incorrect {
  background-color: indianred;
  /*filter: drop-shadow(0 0 3rem indianred)*/
}

.icon {
  height: 100%;
  width: 100%;
}

h2 {
  height: 100%;
  width: 100%;
}

.qIcon {
  height: 15%;
  width: 15%;
}

.selected {
  filter: drop-shadow(0 0 0.75rem dodgerblue)
}

.selectedBtn {
  background-color: lightgray;
}

.submitBtn {
  height: 3rem;
  width: 50%;
}

.answerWrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  max-width: 30rem;
}

.answerWrapper > div {
  height: 10rem;
  width: 10rem;
  margin: 1rem;
}

.answerWrapper > div > h2{
  align-self: center;
}

